import React, { Component } from 'react';
import axios from 'axios';

import '../index.css'

export class Category extends Component {
    state = {
        jokeCategories: [],
        joke: ''
    }

    componentDidMount() {
        axios.get('https://api.chucknorris.io/jokes/categories')
            .then(res => this.setState({jokeCategories: res.data}))
    }

    getJoke = e => {
        e.preventDefault();
        let category = e.target.name;
        axios.get(`https://api.chucknorris.io/jokes/random?category=${category}`)
            .then(res => this.setState({joke: res.data.value}));
    }

    render() {
        let categories = this.state.jokeCategories.map((category, index) => {
        return <button style={btnStyle} key={index} name={category} onClick={this.getJoke} >{category}</button>
        })
        return (
            <div>
                <div>{categories}</div>                
                <p style={jokeStyle}>{this.state.joke}</p>
            </div>
        )
    }
}

const btnStyle = {
    padding: '5px',
    margin: '5px',
    color: '#f4f4f4',
    backgroundColor: '#c9960a',
    fontSize: '24px',
    borderRadius: '5px',
    borderColor: '#c9960a',
    cursor: 'pointer'
}

const jokeStyle = {
    color: '#c9960a',
    padding: '3px',
    marginTop: '5px',
    fontSize: '24px'
}

export default Category;
